import React from "react";
import { Field } from "formik";
import { Link } from "react-router-dom";
import { TextField, Button, Typography } from "@material-ui/core";
import { Select } from "material-ui-formik-components/Select";
import { withStyles } from "@material-ui/core/styles";
import LuxonUtils from "@date-io/luxon";
import {
  DatePicker,
  TimePicker,
  MuiPickersUtilsProvider
} from "@material-ui/pickers";
import { minsToHours } from "../../services/utils";

function BookingForm(props) {
  const {
    classes,
    handleSubmit,
    handleChange,
    values,
    errors,
    typesOfCleaning
  } = props;

  function handleCalcPrice() {
    props.calculateTimePrice({ values, typesOfCleaning });
  }

  const datePickerComponent = ({
    form: { setFieldValue },
    field: { name, value }
  }) => (
    <DatePicker
      id="startDate"
      name={name}
      value={value}
      onChange={value => setFieldValue(name, value)}
      format="dd/MM/yyyy"
      disablePast
      className={classes.input}
      label="  "
      showTodayButton
      okLabel="OK"
      cancelLabel=""
      todayLabel=""
      autoOk
    />
  );

  const timePickerComponent = ({
    form: { setFieldValue },
    field: { name, value }
  }) => (
    <TimePicker
      id="expectedTime"
      value={value}
      onChange={value => setFieldValue(name, value)}
      className={classes.input}
      label="  "
      ampm={false}
      okLabel="OK"
      cancelLabel=""
    />
  );

  let availableWorkingDays = [];
  if (props.availableWorkingDays) {
    if (props.availableWorkingDays["0"])
      availableWorkingDays.push({ value: 0, label: "" });
    if (props.availableWorkingDays["1"])
      availableWorkingDays.push({ value: 1, label: "" });
    if (props.availableWorkingDays["2"])
      availableWorkingDays.push({ value: 2, label: "" });
    if (props.availableWorkingDays["3"])
      availableWorkingDays.push({ value: 3, label: "" });
    if (props.availableWorkingDays["4"])
      availableWorkingDays.push({ value: 4, label: "" });
    if (props.availableWorkingDays["5"])
      availableWorkingDays.push({ value: 5, label: "" });
    if (props.availableWorkingDays["6"])
      availableWorkingDays.push({ value: 6, label: "" });
  }

  let availableServices = [];
  let availableTypes = [];
  if (typesOfCleaning) {
    if (typesOfCleaning.pool > 0)
      availableServices.push({ value: "pool", label: " " });
    if (typesOfCleaning.carpet.isAvailable)
      availableServices.push({ value: "carpet", label: " " });
    if (typesOfCleaning.furniture > 0)
      availableServices.push({
        value: "furniture",
        label: " "
      });

    if (typesOfCleaning.standart.isAvailable)
      availableTypes.push({ value: "standart", label: "" });
    if (typesOfCleaning.general.isAvailable)
      availableTypes.push({ value: "general", label: "" });
    if (typesOfCleaning.afterRepair.isAvailable)
      availableTypes.push({ value: "afterRepair", label: " " });
    if (typesOfCleaning.office > 0)
      availableTypes.push({ value: "office", label: " " });
    if (typesOfCleaning.industrial > 0)
      availableTypes.push({ value: "industrial", label: "" });
  }

  return (
    <div className={classes.root}>
      <MuiPickersUtilsProvider utils={LuxonUtils}>
        <form className={classes.container} onSubmit={handleSubmit}>
          {!props.company ? (
            <TextField
              required
              id="city"
              label=""
              className={classes.input}
              value={values.city}
              onChange={handleChange}
              margin="normal"
            />
          ) : null}
          <TextField
            required
            id="adress"
            label=""
            className={classes.input}
            value={values.adress}
            onChange={handleChange}
            margin="normal"
          />
          <Field
            required
            fullWidth={false}
            name="type"
            label=" "
            className={classes.input}
            options={
              props.company
                ? availableTypes
                : [
                    { value: "standart", label: "" },
                    { value: "general", label: "" },
                    { value: "afterRepair", label: " " },
                    { value: "office", label: " " },
                    { value: "industrial", label: "" }
                  ]
            }
            component={Select}
          />
          {(values.type === "standart") |
          (values.type === "general") |
          (values.type === "afterRepair") ? (
            <>
              <TextField
                required
                id="smallRooms"
                label="-  "
                className={classes.input}
                value={values.smallRooms}
                onChange={handleChange}
                margin="normal"
              />
              <TextField
                required
                id="bigRooms"
                label="-  "
                className={classes.input}
                value={values.bigRooms}
                onChange={handleChange}
                margin="normal"
              />
              <TextField
                required
                id="bathRooms"
                label="- "
                className={classes.input}
                value={values.bathRooms}
                onChange={handleChange}
                margin="normal"
              />
            </>
          ) : (values.type === "office") | (values.type === "industrial") ? (
            <TextField
              required
              id="squareMeters"
              label=" (. .)"
              className={classes.input}
              value={values.squareMeters}
              onChange={handleChange}
              margin="normal"
            />
          ) : null}
          <Field
            multiple
            name="service"
            label=" "
            options={
              props.company
                ? availableServices
                : [
                    { value: "pool", label: " " },
                    { value: "carpet", label: " " },
                    { value: "furniture", label: " " }
                  ]
            }
            component={Select}
          />
          {values.service.indexOf("carpet") !== -1 ? (
            <>
              <TextField
                required
                id="smallCarpets"
                label="-  "
                className={classes.input}
                value={values.smallCarpets}
                onChange={handleChange}
                margin="normal"
              />
              <TextField
                required
                id="bigCarpets"
                label="-  "
                className={classes.input}
                value={values.bigCarpets}
                onChange={handleChange}
                margin="normal"
              />
            </>
          ) : null}
          <Field component={datePickerComponent} name="startDate" />
          <Field component={timePickerComponent} name="expectedTime" />
          <Field
            multiple
            fullWidth={false}
            className={classes.input}
            name="cleaningDays"
            label=" "
            options={
              props.company
                ? availableWorkingDays
                : [
                    { value: 0, label: "" },
                    { value: 1, label: "" },
                    { value: 2, label: "" },
                    { value: 3, label: "" },
                    { value: 4, label: "" },
                    { value: 5, label: "" },
                    { value: 6, label: "" }
                  ]
            }
            component={Select}
          />
          <Field
            required
            fullWidth={false}
            className={classes.input}
            name="regularity"
            label=""
            options={[
              { value: 0, label: " " },
              { value: 1, label: " " },
              { value: 2, label: " 2 " },
              { value: 3, label: " " }
            ]}
            component={Select}
          />
          {values.regularity !== 0 ? (
            <Field
              required
              fullWidth={false}
              className={classes.input}
              name="recurrence"
              label=""
              options={[
                { value: 1, label: "2 " },
                { value: 2, label: "1 " },
                { value: 3, label: "2 " },
                { value: 4, label: "3 " },
                { value: 5, label: "4 " },
                { value: 6, label: "5 " },
                { value: 7, label: "6 " }
              ]}
              component={Select}
            />
          ) : null}

          {props.isAuthenticated ? null : (
            <>
              <TextField
                required
                id="email"
                label=" "
                className={classes.input}
                value={values.email}
                onChange={handleChange}
                margin="normal"
              />
              <TextField
                required
                label=" "
                autoComplete="tel"
                className={classes.input}
                margin="normal"
                id="phoneNumber"
                name="phoneNumber"
                onChange={handleChange}
                value={values.phoneNumber}
                helperText={errors.phoneNumber}
                error={Boolean(errors.phoneNumber)}
              />
            </>
          )}
          {props.company ? (
            <>
              <Button
                onClick={handleCalcPrice}
                variant="outlined"
                color="primary"
                fullWidth
              >
                   
              </Button>
              {props.time && props.price ? (
                <div className={classes.priceAndTime}>
                  <Typography className={classes.calcTitle}>
                    <b>  :</b> {props.price}
                  </Typography>
                  <Typography className={classes.calcTitle}>
                    <b>:</b> {minsToHours(props.time)}
                  </Typography>
                </div>
              ) : null}
              <Button
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
                className={classes.button}
              >
                 
              </Button>
            </>
          ) : (
            <Button type="submit" variant="contained" color="primary" fullWidth>
               
            </Button>
          )}
          {props.company ? (
            <>
              <Button
                component={Link}
                color="primary"
                to={{ pathname: `/companies/${props.company}` }}
              >
                  
              </Button>
              <Button onClick={props.resetSelectedCompany} color="secondary">
                 
              </Button>
            </>
          ) : null}
        </form>
      </MuiPickersUtilsProvider>
    </div>
  );
}

const styles = theme => ({
  root: {
    marginTop: 20,
    backgroundColor: "whitesmoke",
    boxShadow: "0 1px 7px 1px rgba(0, 0, 0, .25)",
    padding: 25,
    fontSize: "1.2rem",
    display: "flex",
    justifyContent: "center"
  },
  container: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "center",
    width: 550
  },
  input: {
    width: 240,
    marginRight: 24
  },
  button: {
    margin: theme.spacing.unit
  },
  priceAndTime: {
    display: "flex"
  },
  calcTitle: {
    marginLeft: 8,
    marginTop: 8
  }
});

export default withStyles(styles)(BookingForm);
