import React, { Component } from "react";
import { Formik } from "formik";
import { string, object } from "yup";
import { TextField, withStyles, Button } from "@material-ui/core";
import teal from "@material-ui/core/colors/teal";
import VerificationCodeField from "../../../components/auth/VerificationCodeField";

const validationSchema = object().shape({
  username: string()
    .required("  ")
    .min(2, "     2 ")
    .max(9, "     10 ")
    .matches(
      /^[a-zA-Z][a-zA-Z0-9-_.]{1,9}$/,
      "    , , -, ., _"
    ),
  password: string()
    .required("  ")
    .min(5, "    4 ")
    .max(18, "    19 ")
    .matches(/^[\S]{5,18}$/, "    "),
  confirmPassword: string()
    .required("    ")
    .test("passwords-match", "  ", function (value) {
      return this.parent.password === value;
    }),
  email: string()
    .required("  ")
    .min(5, ".     4 ")
    .max(50, ".     51 ")
    .matches(
      /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/,
      ".  "
    ),
  phoneNumber: string()
    .required("  ")
    .min(13, "  , : +375296667788")
    .max(13, "  , : +375296667788")
    .matches(
      /\+375(29|33|44|25)\d{7}$/,
      "  , : +375296667788"
    ),
  adress: string()
    .required("  ")
    .min(6, "    5 ")
    .max(60, "  ")
});

class UserSignUp extends Component {
  constructor(props) {
    super(props);

    this.state = {
      verificationCode: "",
      isSended: false
    };
  }

  handleMessage = (msg, variant) => {
    this.props.enqueueSnackbar(msg, { variant });
  };

  handleVerificationCodeChange = code => {
    this.setState({ verificationCode: code });
  };

  handleNewVerificationCode = () => {
    this.props.userNewVerificationCode(this.props.username);
  };

  handleConfirm = () => {
    this.props.confirmUser({
      username: this.props.username,
      verificationCode: this.state.verificationCode
    });
  };

  render() {
    const { classes } = this.props;
    return (
      <>
        <Formik
          initialValues={{
            username: "",
            password: "",
            email: "",
            phoneNumber: ""
          }}
          onSubmit={(values, { setFieldError }) => {
            try {
              this.props.signUpUser(values);
            } catch (errors) {
              errors.forEach(err => {
                setFieldError(err.field, err.error);
              });
            }
          }}
          validationSchema={validationSchema}
          render={({
            values,
            errors,
            handleBlur,
            handleChange,
            handleSubmit
          }) => (
            <form
              className={classes.container}
              onSubmit={handleSubmit}
              noValidate
            >
              {!this.props.isSended && (
                <>
                  <TextField
                    label=" "
                    autoComplete="username"
                    disabled={this.props.isSended}
                    className={classes.textField}
                    margin="normal"
                    variant="outlined"
                    name="username"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.username}
                    helperText={errors.username}
                    error={Boolean(errors.username)}
                  />
                  <TextField
                    label=""
                    autoComplete="new-password"
                    disabled={this.props.isSended}
                    className={classes.textField}
                    margin="normal"
                    variant="outlined"
                    type="password"
                    name="password"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.password}
                    helperText={errors.password}
                    error={Boolean(errors.password)}
                  />
                  <TextField
                    label=" "
                    autoComplete="new-password"
                    disabled={this.props.isSended}
                    className={classes.textField}
                    margin="normal"
                    variant="outlined"
                    type="password"
                    name="confirmPassword"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.confirmPassword}
                    helperText={errors.confirmPassword}
                    error={Boolean(errors.confirmPassword)}
                  />
                  <TextField
                    label=" "
                    autoComplete="email"
                    disabled={this.props.isSended}
                    className={classes.textField}
                    margin="normal"
                    variant="outlined"
                    type="email"
                    name="email"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.email}
                    helperText={errors.email}
                    error={Boolean(errors.email)}
                  />
                  <TextField
                    label=" "
                    autoComplete="tel"
                    disabled={this.props.isSended}
                    className={classes.textField}
                    margin="normal"
                    variant="outlined"
                    name="phoneNumber"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.phoneNumber}
                    helperText={errors.phoneNumber}
                    error={Boolean(errors.phoneNumber)}
                  />
                  <TextField
                    label=" "
                    autoComplete="tel"
                    disabled={this.props.isSended}
                    className={classes.textField}
                    margin="normal"
                    variant="outlined"
                    name="adress"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.adress}
                    helperText={errors.adress}
                    error={Boolean(errors.adress)}
                  />
                </>
              )}
              <div className={classes.VerifyAndConfirmContainer}>
                {this.props.isSended && (
                  <>
                    <VerificationCodeField
                      value={this.state.verificationCode}
                      handleChange={this.handleVerificationCodeChange}
                    />
                    <Button
                      onClick={this.handleConfirm}
                      key="submit"
                      variant="contained"
                      color="primary"
                      size="large"
                      className={classes.confirmButton}
                    >
                      CONFIRM
                    </Button>
                    <Button
                      size="small"
                      onClick={this.handleNewVerificationCode}
                      className={classes.margin}
                    >
                        
                    </Button>
                  </>
                )}
                {!this.props.isSended && (
                  <Button
                    onClick={handleSubmit}
                    type="submit"
                    key="submit"
                    variant="contained"
                    color="primary"
                    size="large"
                    className={classes.button}
                  >
                    
                  </Button>
                )}
              </div>
            </form>
          )}
        />
      </>
    );
  }
}

const styles = theme => ({
  container: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    flexWrap: "wrap",
    flexGrow: 1
  },
  button: {
    margin: theme.spacing.unit
  },
  confirmButton: {
    margin: theme.spacing.unit,
    backgroundColor: teal[500],
    "&:hover": {
      backgroundColor: teal[300]
    }
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 250
  },
  VerifyAndConfirmContainer: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center"
  }
});

export default withStyles(styles)(UserSignUp);
