import React, { Component } from "react";
import { Formik, Field } from "formik";
import { string, object, array } from "yup";
import {
  TextField,
  withStyles,
  Grid,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  Button
} from "@material-ui/core";
import { Select } from "material-ui-formik-components/Select";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

const validationSchema = object().shape({
  username: string()
    .required("  ")
    .min(2, "     2 ")
    .max(9, "     10 ")
    .matches(
      /^[a-zA-Z][a-zA-Z0-9-_.]{1,9}$/,
      "    , , -, ., _"
    ),
  password: string()
    .required("  ")
    .min(5, "    4 ")
    .max(18, "    19 ")
    .matches(/^[\S]{5,18}$/, "    "),
  confirmPassword: string()
    .required("    ")
    .test("passwords-match", "  ", function (value) {
      return this.parent.password === value;
    }),
  email: string()
    .required("  ")
    .min(5, ".     4 ")
    .max(50, ".     51 ")
    .matches(
      /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/,
      ".  "
    ),
  phoneNumber: string()
    .required("  ")
    .min(13, "  ")
    .max(13, "  "),
  city: string()
    .required("  ")
    .min(3, "     2 ")
    .max(14, "     14 ")
    .matches(
      /^[---]{3,14}$/,
      "     , -, "
    ),
  companyName: string()
    .required("  ")
    .min(3, "     2 ")
    .max(20, "     21 "),
  description: string().max(
    80,
    "      80 "
  ),
  workingDays: array()
    .min(1, "    1  ")
    .max(7, " "),
  standartSmallRoom: string().matches(/^\d{1,5}$/, " "),
  standartBigRoom: string().matches(/^\d{1,5}$/, " "),
  standartBathRoom: string().matches(/^\d{1,5}$/, " "),
  generalBathRoom: string().matches(/^\d{1,5}$/, " "),
  generalBigRoom: string().matches(/^\d{1,5}$/, " "),
  generalSmallRoom: string().matches(/^\d{1,5}$/, " "),
  afterRepairBathRoom: string().matches(/^\d{1,5}$/, " "),
  afterRepairBigRoom: string().matches(/^\d{1,5}$/, " "),
  afterRepairSmallRoom: string().matches(/^\d{1,5}$/, " "),
  smallCarpet: string().matches(/^\d{1,5}$/, " "),
  bigCarpet: string().matches(/^\d{1,5}$/, " "),
  office: string().matches(/^\d{1,5}$/, " "),
  furniture: string().matches(/^\d{1,5}$/, " "),
  industrial: string().matches(/^\d{1,5}$/, " "),
  pool: string().matches(/^\d{1,5}$/, " ")
});

