import React, { Component } from "react";
import { withStyles } from "@material-ui/core/styles";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Typography from "@material-ui/core/Typography";
import {
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  TextField,
  DialogActions
} from "@material-ui/core";
import {
  getTranslatedCleaningTypeName,
  minsToHours,
  getTranslatedBookingStatusName
} from "../../../../../services/utils";

class BookingCard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      open: false,
      reason: ""
    };
  }

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleChangeReason = event => {
    this.setState({ reason: event.target.value });
  };

  handleAcceptBook = () => {
    this.props.acceptBook({
      orderId: this.props.booking._id,
      query: this.props.search
    });
  };

  handleCancelBook = () => {
    if (this.props.role === "user") {
      this.props.cancelBook({
        orderId: this.props.booking._id,
        query: this.props.search
      });
    } else if (this.props.role === "executor") {
      this.handleClickOpen();
    }
  };

  handleCancelBookExecutor = () => {
    this.props.cancelBook({
      orderId: this.props.booking._id,
      query: this.props.search,
      reason: this.state.reason
    });
    this.handleClose();
  };

  handleConfirmBook = () => {
    this.props.confirmBook({
      orderId: this.props.booking._id,
      query: this.props.search
    });
  };

  render() {
    const { classes, booking } = this.props;
    const startDate = new Date(booking.startDate);
    const endDate = new Date(booking.endDate);
    const startDateFormat =
      startDate.getDate() +
      "." +
      startDate.getMonth() +
      "." +
      startDate.getFullYear();
    const endDateFormat =
      endDate.getDate() +
      "." +
      endDate.getMonth() +
      "." +
      endDate.getFullYear();

    const Days = [
      "",
      "",
      "",
      "",
      "",
      "",
      ""
    ];
    let days = [];
    booking.cleaningDays.forEach(day => {
      days.push(Days[day]);
    });
    days = days.join(",");

    const RegularityNames = [
      " ",
      " ",
      " 2 ",
      " "
    ];
    const regularity = RegularityNames[booking.regularity];

    const RecurrenceNames = [
      "-",
      "2 ",
      "",
      "2 ",
      "3 ",
      "4 ",
      "5 ",
      "6 "
    ];
    const recurrence = RecurrenceNames[booking.recurrence];

    return (
      <div className={classes.listItem}>
        <ListItem alignItems="flex-start">
          <ListItemText
            primary={getTranslatedCleaningTypeName(booking.type)}
            secondary={
              <>
                <Typography component="div" color="textPrimary">
                  <b>:</b> {booking.city}
                </Typography>
                <Typography component="div" color="textPrimary">
                  <b>:</b> {booking.adress}
                </Typography>
                <Typography component="div" color="textPrimary">
                  <b> :</b> {booking.phoneNumber}
                </Typography>
                {(booking.type === "office") |
                (booking.type === "industrial") ? (
                  <Typography component="div" color="textPrimary">
                    <b> :</b> {booking.squareMeters}
                  </Typography>
                ) : (
                  <>
                    <Typography component="div" color="textPrimary">
                      <b>-  :</b> {booking.smallRooms}
                    </Typography>
                    <Typography component="div" color="textPrimary">
                      <b>-  :</b> {booking.bigRooms}
                    </Typography>
                    <Typography component="div" color="textPrimary">
                      <b>- :</b> {booking.bathRooms}
                    </Typography>
                  </>
                )}
                <Typography component="div" color="textPrimary">
                  <b>  :</b> {startDateFormat}
                </Typography>
                <Typography component="div" color="textPrimary">
                  <b> :</b> {days}
                </Typography>
                <Typography component="div" color="textPrimary">
                  <b>  :</b> {endDateFormat}
                </Typography>
                <Typography component="div" color="textPrimary">
                  <b>:</b> {regularity}
                </Typography>
                <Typography component="div" color="textPrimary">
                  <b>:</b> {recurrence}
                </Typography>
                {this.props.role !== "executor" && (
                  <Typography component="div" color="textPrimary">
                    <b> :</b> {booking.companyName}
                  </Typography>
                )}
                <Typography component="div" color="textPrimary">
                  <b>  :</b> {booking.price}
                </Typography>
                <Typography component="div" color="textPrimary">
                  <b>:</b> {minsToHours(booking.time)}
                </Typography>
                <Typography component="div" color="textPrimary">
                  <b>:</b>{" "}
                  {getTranslatedBookingStatusName(booking.status)}
                </Typography>
                {(this.props.role === "executor") &
                (booking.status === "new") ? (
                  <Button
                    size="small"
                    variant="contained"
                    color="primary"
                    className={classes.button}
                    onClick={this.handleAcceptBook}
                  >
                    
                  </Button>
                ) : null}
                {(this.props.role === "user") &
                (booking.status === "accepted") &
                (new Date(booking.endDate) < new Date()) ? (
                  <Button
                    size="small"
                    variant="contained"
                    color="secondary"
                    className={classes.button}
                    onClick={this.handleConfirmBook}
                  >
                     
                  </Button>
                ) : null}
                {booking.status === "new" ? (
                  <Button
                    size="small"
                    variant="outlined"
                    color="secondary"
                    className={classes.button}
                    onClick={this.handleCancelBook}
                  >
                    
                  </Button>
                ) : null}
                <Dialog
                  open={this.state.open}
                  onClose={this.handleClose}
                  aria-labelledby="form-dialog-title"
                >
                  <DialogTitle id="form-dialog-title">
                     
                  </DialogTitle>
                  <DialogContent>
                    <TextField
                      autoFocus
                      margin="dense"
                      id="name"
                      value={this.state.reason}
                      onChange={this.handleChangeReason}
                      label=""
                      fullWidth
                    />
                  </DialogContent>
                  <DialogActions>
                    <Button onClick={this.handleClose}></Button>
                    <Button
                      onClick={this.handleCancelBookExecutor}
                      variant="outlined"
                      color="secondary"
                    >
                       
                    </Button>
                  </DialogActions>
                </Dialog>
              </>
            }
          />
        </ListItem>
      </div>
    );
  }
}

const styles = theme => ({
  listItem: {
    borderBottom: "2px solid rgba(245,0,87,0.2)",
    borderRadius: "2%"
  },
  AvatarAndSummary: {
    display: "flex"
  },
  logo: {
    width: 125,
    height: 125,
    boxShadow: theme.shadows[2]
  },
  button: {
    margin: theme.spacing.unit
  }
});

export default withStyles(styles)(BookingCard);
